import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    showtransition:true,
    introanime:true,
    menucurrentidx:0,
    scrollbehave:'hidden'
  },
  mutations: {
    changepage(state){
      state.showtransition =  false;
      // state.introanime = false;
    },
    changemenuindex(state,idx){
      state.menucurrentidx = idx;
    },
    changescrollevent(state,behave){
      state.scrollbehave = behave
    }

  },
  actions: {
    detechmenuindex(state,idx){
      state.menucurrentidx = idx;
    }
   
  },
  modules: {
  }
})
