    changescrollevent() {
      let allwidth = document.querySelector(".home").offsetWidth;
      console.log(window.scrollY);
      if (window.scrollY < allwidth / 3) {
        this.$store.commit("changescrollevent", "hidden");
      }
      if (
        window.scrollY >= allwidth / 3 &&
        window.scrollY < (allwidth / 3) * 2
      ) {
        this.$store.commit("changescrollevent", "hidden");
      }
      if (window.scrollY > (allwidth / 3) * 2 && window.scrollY < allwidth) {
        this.$store.commit("changescrollevent", "scroll");
      }
      if (window.scrollY >= allwidth) {
        this.$store.commit("changescrollevent", "hidden");
      }
    },
    catchcurrentidx(idx) {
      this.catchindex = idx;
    },
    settest(idx) {
      let font;
      if (idx == 1) {
        font = this.$refs.standardfont1;
      }
      if (idx == 2) {
        font = this.$refs.standardfont2;
      }
      if (idx == 3) {
        font = this.$refs.standardfont3;
      }
      if (idx == 4) {
        font = this.$refs.standardfont4;
      }
      if (idx == 5) {
        font = this.$refs.standardfont5;
      }
      if (idx == 6) {
        font = this.$refs.standardfont6;
      }
      if (idx == 7) {
        font = this.$refs.standardfont7;
      }
      if (idx == 8) {
        font = this.$refs.standardfont8;
      }

      let startClient = {
        x: event.clientX,
        y: event.clientY
      };

      let newSVGPoint = font.createSVGPoint();
      let CTM = font.getScreenCTM();
      newSVGPoint.x = startClient.x;
      newSVGPoint.y = startClient.y;
      let startSVGPoint = newSVGPoint.matrixTransform(CTM.inverse());

      gsap.to(font, {
        top: -startSVGPoint.y * 0.1,
        left: -startSVGPoint.x * 0.1,
        ease: "power2.out",
        duration: 0.3
      });
    },
    settest2(idx) {
      console.log(idx);
      // let font;
