      // }
      // gsap.to(font, {
      //   left: left,
      //   top: top,
      //   duration: 0.3,
      //   ease: "power2.out",
      // });
    },
    test3() {
      gsap.to(this.$refs.standardfont1, {
        left: 0,
        top: 0,
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont2, {
        left: "-30px",
        top: 0,
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont3, {
        left: "-70px",
        top: "25px",
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont4, {
        left: "-110px",
        top: 0,
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont5, {
        left: "10px",
        top: "30px",
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont6, {
        left: "-40px",
        top: 0,
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont7, {
        left: "-90",
        top: 0,
        duration: 0.4,
        ease: "power2.out"
      });
      gsap.to(this.$refs.standardfont8, {
