<script>
import gsap from "gsap";
import anime from "animejs/lib/anime.es.js";
// import axios from "axios";
export default {
  props: ["currentclip"],
  data() {
    return {
      articlenum: 0,
      isact: false,
      menulist: ["MAIN", "CLIP-1", "CLIP-2", "CLIP-3", "ABOUT"],
      currentpage: ""
    };
  },
  created() {
    window.addEventListener("scroll", this.changemenuitem);
  },
  mounted() {
    gsap.to("li", {
      y: 0,
      stagger: 0.1,
      delay: 5.1
    });
    anime({
      targets: document.querySelectorAll(".time .text1"),
      translateY: "-120px",
      easing: "easeInOutQuad",
      direction: "alternate",
      duration: 1000,
      delay: anime.stagger(300),
      // delay: 1000,
      loop: true
    });
    anime({
      targets: document.querySelectorAll(".time .text2"),
      translateY: "-120px",
      easing: "easeInOutQuad",
      direction: "alternate",
      duration: 1000,
      delay: anime.stagger(300),
      // delay: 1000,
      loop: true
    });
  },
  methods: {
    openmenu(){
      this.$emit('openmobilemenu')
    },
    changemenuitem() {
      let allwidth = document.querySelector(".home").offsetWidth;
      if (window.scrollY < allwidth / 4) {
        this.currentpage = "MAIN";
        this.$store.commit("changemenuindex", 0);
      }
      if (window.scrollY >= (allwidth / 4) && window.scrollY <  (allwidth / 4)*2) {
        this.currentpage = "CLIP-1";
        this.$store.commit("changemenuindex", 1);
      }
      if (window.scrollY >= (allwidth / 4)*2 && window.scrollY < (allwidth / 4)*3) {
        this.currentpage = "CLIP-2";
        this.$store.commit("changemenuindex", 2);
      }
      if (window.scrollY >= (allwidth / 4)*3 && window.scrollY < allwidth) {
        this.currentpage = "CLIP-3";
        this.$store.commit("changemenuindex", 3);
      }
      if(window.scrollY >=allwidth){
        this.currentpage = "ABOUT";
        this.$store.commit("changemenuindex", 4);
      }
    },
    
    topage(page, idx) {
      let allwidth = document.querySelector(".home").offsetWidth;
      if (page == "MAIN") {
        window.scrollTo(0, 0);
        this.$store.commit("changemenuindex", idx);
      }
      if (page == "CLIP-1") {
        window.scrollTo(0, (allwidth / 4));
        this.$store.commit("changemenuindex", idx);
      }
      if (page == "CLIP-2") {
        window.scrollTo(0, (allwidth / 4)*2);
        this.$store.commit("changemenuindex", idx);
      }
      if (page == "CLIP-3") {
        window.scrollTo(0, (allwidth / 4)*3);
        this.$store.commit("changemenuindex", idx);
      }
      if(page=="ABOUT"){
        window.scrollTo(0, allwidth);
        this.$store.commit("changemenuindex", idx);
      }
    }
  },
  computed: {
    menucurrentidx() {
      return this.$store.state.menucurrentidx;
    }
  }
};
</script>


